.container_header {
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 0 25px;

  @media screen and(min-width:767px) and(max-width:1279px) {
    padding: 0 87px;
    // width: getRange('tablet');
  }
  @media screen and(min-width:1280px) {
    padding: 0 175px;
    // width: getRange('desktop');
  }

  &.nopad {
    padding: 0;
  }
}
.search_form {
  @media screen and(max-width:767px) {
    align-items: center;
    display: flex;
    flex-direction: column;
    // padding: 25px 0 112px;
    position: relative;
    animation: translating 2s ease 0.2s;
  }
  @media screen and(min-width: 768px) and (max-width: 1279px) {
    display: flex;
    justify-content: space-between;
    width: 570px;
    margin: 0 auto;
  }
  @media screen and (min-width: 1280px) {
    display: flex;
    justify-content: center;
  }
}

.search_field {
  padding-bottom: 25px;
  position: relative;
  & input:hover,
  & input:focus,
  & input.active {
    // outline: none;
    box-shadow: 0 0 3px 3px $title-text-color;
    // background-color: $title-text-color;
    @media screen and(min-width:768px) and(max-width:1279px) {
      color: #ffff;
    }
  }
}
.searchInput {
  width: 270px;
  height: 40px;
  padding: 10px 30px;
  background: $search-bg-color;
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid $search-bg-color;
  color: $accent-color;
  outline: none;
  font-size: 16px;
  line-height: 1.25;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and(min-width:1280px) {
    margin-right: 30px;
  }
  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: $font-size-16;
    line-height: 1.25px;
    color: $primary-text-color;
  }
  &::-webkit-input-placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $accent-color;

    &::-moz-placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 2.5;
      color: $accent-color;
    }
    &:-moz-placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 2.5;
      color: $primary-text-color;
    }
    &:-ms-input-placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 1.25;
      color: $primary-text-color;
    }
    &:hover,
    &:focus {
      box-shadow: 0px 0px 10px 3px $title-text-color;
      color: $primary-text-color;
      // background-color: $title-text-color;
    }

    &:hover::-webkit-input-placeholder,
    &:focus::-webkit-input-placeholder {
      color: $primary-text-color;
    }
    &:hover::-moz-placeholder,
    &:focus::-moz-placeholder {
      color: $primary-text-color;
    }
    &:hover:-moz-placeholder,
    &:focus:-moz-placeholder {
      color: $primary-text-color;
    }
    &:hover:-ms-input-placeholder,
    &:focus:-ms-input-placeholder {
      color: $primary-text-color;
    }
  }
}

.countrySearch {
  width: 270px;
  height: 40px;
  padding: 10px 30px;
  background: $search-bg-color;
  backdrop-filter: blur(20px);
  border-radius: 20px;
  transition: color 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  border: transparent;
  color: $accent-color;
  outline: none;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    box-shadow: 0px 0px 10px 3px $title-text-color;
    color: $accent-color;
  }
}
label {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 50px;
    transform: translateY(-50%);
    display: inline-block;
    background: url('../images/searchInput.svg') no-repeat;
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
    padding-top: 14px;
  }
}
